import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
const users = ["adrian", "Oomasz", "Janusz", "Tomasz", "ofiarnusz"];

class App extends Component {
  constructor(){
    super();
    this.state = ({
      users
    })
  }
  handleChange(e){
    const text = e.currentTarget.value;
    const users = this.handleSearch(text);
    this.setState({
      users
    })
    
  };
  handleSearch( word ){
    return users.filter(user => user.toLowerCase().includes(word.toLowerCase()))
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
          <input onInput={this.handleChange.bind(this)}></input>
          <UsersList users={this.state.users} />
        </header>
      </div>
    );
  }
}

const UsersList = ({ users }) => {
      if (users.length > 0) {
        return (
            <ul>
              {users.map(user => <li key={user}>{user}</li>)}
            </ul>
          );
      }
      return(
        <p>not found</p>
      )
  }

export default App;
